import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Navigation from '../../components/nav/Navigation';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
import { setUsernameActionCreator, setPasswordActionCreator } from '../../reducers/auth/authActionCreators';
import styles from './RegisterPageStyles';

class RegisterPage extends Component {
    static propTypes = {
      classes: PropTypes.object.isRequired,
      setUsername: PropTypes.func.isRequired
    }

    state = {
      password: ''
    };

    render() {
      const { classes, setUsername } = this.props;
      return (
        <Paper className={classes.root}>
          <Navigation />
          <TextField
            color="inherit"
            type="email"
            label="Email"
            placeholder="Enter your Email"
            className={classes.textField}
            onChange={(event, username) => setUsername(username)}
          />
          <br/>
          <TextField
            color="inherit"
            type="password"
            label="Password"
            placeholder="Enter your Password"
            className={classes.textField}
            onChange={(event, password) => setUsername(password)}
          />
          <br/>
          <Button
            label="Submit"
            style={styles}
            variant="contained"
            color="primary"
            className={classes.button}
            onClick={event => this.handleClick(event)}
          >
            Register
          </Button>
        </Paper>
      );
    }
}

const StyledRegisterPage = withStyles({})(RegisterPage);

const mapStateToProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToProps = dispatch => ({
  setUsername: () => dispatch(setUsernameActionCreator()),
  setPassword: () => dispatch(setPasswordActionCreator())
});
const ReduxRegisterPage = connect(
  mapStateToProps,
  mapDispatchToProps
)(StyledRegisterPage);

export default ReduxRegisterPage;
